#header {
    z-index: 3;
    position: relative;
    width: 100%;
    height: 156px;
    background: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,  0.07);    
    .row {
        width: 100%;
        background: #fff;            
        &.row1 {
            height: 36px;
            .container {
                width: 100%;
                max-width: 1050px;
                height: 100%;
                margin: 0 auto;  
                display: flex;
                justify-content: flex-end;
                align-items: center;          
                #aside {
                    ul {
                        display: flex;
                        li {
                            font-size: 12px;
                            a {
                                color: rgb(51,51,51);                   
                                span {
                                    margin-left: 5px;
                                    img {
                                        width: 8px;
                                    }
                                }
                            }
                            &:first-child {
                                a {
                                    color: rgb(95,0, 128);
                                    font-weight: 500;    
                                }
                            }
                            i {
                                margin: 0 10px;
                                color: #ccc;
                            }
                        }
                    }
                }
            }
        }
        &.row2 {
            height: 64px;
            .container {
                width: 100%;
                max-width: 1050px;
                height: 100%;
                margin: 0 auto; 
                display: flex;
                align-items: center;
                > div {
                    height: 48px;                    
                    &.left {
                        width: 100%;
                        max-width: 325px;
                        margin: 0 auto 0 0;
                        display: flex;
                        align-items: center;
                        h1 {
                            span{
                                margin-right: 10px;
                                img {
                                    width: 82px;
                                }
                            }
                            a {
                                color: rgb(95,0, 128);
                                font-size: 18px;
                                font-weight: 600;    
                            }
                        }
                        i {
                            margin: 0 15px;
                            color: #ccc;
                        }
                        > a {
                            color:rgb(181,181,181);
                            font-size: 18px;
                            font-weight: 600;
                        }
                    }
                    &.center {
                        width: 100%;
                        min-width: 250px;
                        max-width: 400px;
                        margin: 0 auto 0 auto;
                        position: relative;
                       
                        input {
                            z-index: 1;
                            position: relative;
                            width: 100%;
                            height: 100%;
                            border: 1px solid rgb(95,0, 128);
                            font-size: 16px;
                            border-radius: 5px;
                            padding-left: 15px;
                            color: rgb(51,51,51);
                            &::placeholder {
                                color:#999;
                            }
                        }
                        
                        button {
                            z-index: 2;
                            position: absolute;
                            top: 0;
                            right: 0;
                            background: transparent;
                            border: 0;
                            height: 100%;
                            width: 48px;
                            cursor: pointer;
                            img {
                                width: 35px;
                            }
                        }

                        &.on {
                            z-index: 4;
                            position: fixed;
                            right: 50%;
                            top: 10px;
                            min-width: 150px;
                            max-width: 220px;
                            height: 36px;
                            margin-right: -320px;
                            input {
                                border-width: 0;  
                                background: rgba(0,0,0, 0.05);
                                &::placeholder {
                                    color:#999;
                                    font-size: 11px;
                                }
                            }
                            button {
                                width: 36px;
                                img {
                                    width: 25px;
                                }
                            }
                        }
                        

                    }
                    &.right {
                        width: 100%;
                        max-width: 325px;
                        margin: 0 0 0 auto;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        a {
                            margin-left:20px;
                            img {
                                width: auto;
                            }
                        }
                        &.on{
                            z-index: 4;
                            position: fixed;
                            right: 50%;
                            top: (56px - 48px) / 2;
                            margin-right: -(1050px / 2);
                        }
                    }
                }
            }
        }
        &.row3 {
            height: 56px;
            &.on {
                z-index: 3;
                position: fixed;
                top: 0;
                left: 0;
                width: 100%;
                background: #fff;        
                box-shadow: 0 3px 6px rgba(0,0,0,  0.07);             
            }
            .container {
                width: 100%;
                max-width: 1050px;
                height: 100%;
                margin: 0 auto; 
                display: flex;
                justify-content: space-between;
                > div {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    &.left {
                        width: 100%;
                        max-width: 225px;
                        justify-content: flex-start;
                        &.on {
                            max-width: 120px;
                        }
                        h1 {
                            a {                                
                                img {
                                    width: auto;
                                }
                                span {
                                    font-size: 16px;
                                    color: rgb(90,90,90); 
                                    margin-left: 10px;
                                    font-weight: 600;
                                }
                                &:hover {
                                    span {
                                        color: rgb(95,0,128); 
                                    }
                                }
                            }
                        }
                    }
                    &.center {
                        width: 100%;
                        max-width: 600px;
                        &.on{
                            max-width: 480px;
                            margin: auto auto auto 0;
                        }
                        #nav {
                            width: 100%;
                            ul {
                                width: 100%;
                                display: flex;
                                li {
                                    width: 25%;
                                    text-align: center;
                                    a {
                                        font-size: 16px;
                                        font-weight: 600;
                                        color: rgb(90,90,90);
                                        padding-bottom: 2px;
                                        border-bottom: 1px solid transparent;
                                        &:hover {
                                            color: rgb(95,0,128);                                            
                                            border-color: rgb(95,0, 128);
                                        }
                                    }
                                }
                            }
                        }
                    }
                    &.right {
                        width: 100%;
                        max-width: 225px;
                        display: flex;
                        justify-content: flex-end;
                        &.on{
                            display: none;
                        }
                        span {
                            font-size: 13px;
                            padding: 5px 10px;
                            border: 1px solid #ccc;
                            border-radius: 12.0px;
                            strong {
                                color: rgb(95,0, 128);
                            }
                            em {
                                color: rgb(102,102, 102);
                                margin-left: 5px;
                            }
                        }
                    }
                }
            }
        }        
    }
}