#topModal {
    width: 100%;
    height: 42px;
    background: rgba(95,0, 128, 1);
    .container {
        position: relative;
        width: 100%;
        max-width: 1050px;
        height: 100%;
        margin: 0 auto;
        > a {
            z-index: 1;
            position: relative;
            display: flex;
            width: 100%;
            height: 100%;
            font-size: 14px;
            color: #fff;
            justify-content: center;
            align-items: center;
            span {font-weight: 500;}
            strong {font-weight: 600;}
        }
        button {
            z-index: 2;
            position: absolute;
            right:0;
            top:0;            
            width: 42px;
            height: 100%;
            background: transparent;
            border: 0;
            cursor: pointer;
            svg {
                width: 20px;
                height: 20px;
            }
        }
    }
}