#section1 {
    width:100%;
    .slide-container {
        width:100%;
        max-width: 1900px;
        margin: 0 auto;
        position: relative;
        .slide-view {
            z-index: 1;
            position: relative;            
            width:100%;
            overflow: hidden;
            .slide-wrap {
                width: 100% * 27; 
                display: flex;
                margin-left: -100%;
                position: relative; 
                left: -100% * 0;
                transition: all 0.6s ease-in-out;              
                .slide {
                    width:100%;
                    a {
                        display: block;
                        width:100%;
                        img {
                            width:100%;
                        }
                    }                    
                }
            }
        }
        .slide-btn {
            z-index: 2;
            position: absolute;
            top:50%;
            margin-top: -26px;
            width: 52px;
            height: 52px;
            background: none;
            border: 0;
            border-radius: 50%;
            cursor: pointer;
            transition: all 1s;
            opacity: 0; 
            &.on {
                opacity: 1; 
            }
            &.right-btn {
                right: 15%;
            }
            &.left-btn {
                left: 15%;
                img {
                    transform: rotate(180deg);
                }
            }
            img {
                width:100%;
                height:100%;
            }
        }
        .count-box {
            user-select: none;
            z-index: 2;
            position: absolute;
            bottom: 35px;
            right:27%;
            padding: 3px 15px 6px 15px;
            background: rgba(0,0,0, 0.3);
            color:#fff;
            font-size: 14px;
            border-radius: 11.5px;
            i {
                margin: 0 5px;
            }
        }
    }
}