#section2,
#section6{
    width: 100%;
    padding-top: 90px;
    background: #fff;
    .container {
        width: 100%;
        max-width: 1050px;
        margin: 0 auto;
        .title {
            width: 100%;
            text-align: center;
            h2{
                font-size: 28px;
                font-weight: 600;
                color:#333;  
                padding-bottom: 15px;              
            }
            h3{
                font-size: 16px;
                font-weight: 400;
                color:#999;    
            }
        }
        .content {
            width: 100%;
            padding-top: 40px;
            .slide-container{
                width: 100%;
                position: relative;
                .slide-view {
                    z-index: 1;
                    position: relative;
                    width: 100%;
                    overflow: hidden;
                    .slide-wrap {
                        width: 1068px * 5;
                        display: flex;
                        position: relative;
                        .slide {
                            width: 25%;
                            a {
                               width: auto; 
                                display: block;
                                margin-right: 18px;                                
                                .box {
                                    width: 100%;
                                    overflow: hidden;
                                    &.row1-box {
                                        position: relative;
                                        span {
                                            z-index: 1;
                                            position: relative;
                                            display: block;
                                            width: 100%;
                                            img{
                                                width: 100%;
                                                transition: all 0.6s;
                                            }
                                        }
                                        em {
                                            z-index: 2;
                                            position: absolute;
                                            left:0;
                                            bottom: 0;
                                            display: block;
                                            width: 125px;
                                            img{
                                                width: 100%;
                                                transition: all 0.6s;
                                            }
                                        }
                                        &:hover {
                                            span {                                               
                                                img{
                                                    transform: scale(1.08);
                                                }
                                            } 
                                            em {                                               
                                                img{
                                                    transform: scale(1.08);
                                                }
                                            } 
                                        }
                                    }
                                    &.row2-box {                                        
                                        padding-top: 6px;
                                        ul {
                                            width: 100%;
                                            li {
                                                width: 100%;
                                                line-height: 180%;
                                                &:nth-child(1){
                                                    button {
                                                        width: 100%;
                                                        height: 35px;
                                                        border: 1px solid #ddd;
                                                        background: rgb(255,255,255);
                                                        border: 1px solid rgb(221,221,221);
                                                        cursor: pointer;
                                                        svg {
                                                            width:22px;
                                                            margin-right: 3px;
                                                        }
                                                        em {
                                                            font-size: 16px;
                                                        }
                                                    }
                                                }
                                                &:nth-child(2){
                                                    padding-top: 10px;
                                                    h3 {
                                                        font-size: 16px;
                                                        font-weight: 500;
                                                    }
                                                }
                                                &:nth-child(3){                                                    
                                                    span {
                                                        color: #999;
                                                        font-size: 14px;
                                                        text-decoration: line-through;
                                                    }
                                                }
                                                &:nth-child(4){
                                                    strong {
                                                        font-size: 16px;
                                                        font-weight: 600;
                                                        &:nth-child(1) {
                                                            color: rgb(250,98,47);
                                                        }
                                                        &:nth-child(2) {
                                                            margin-left: 10px;
                                                            color: rgb(34,34,34);
                                                        }
                                                    }
                                                }
                                                &:nth-child(5){
                                                    em {
                                                        color: rgb(168,177,188);
                                                        svg {
                                                            width:15px;
                                                            margin-right: 5px;
                                                        }                                                        
                                                    }
                                                }
                                                
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .arrow-btn {
                    z-index: 2;
                    position: absolute;
                    top: (324px - 60px) / 2;
                    display: block;
                    width: 60px;
                    height: 60px;
                    border-radius: 50%;                    
                    &.right-btn {
                        right:-30px;
                        opacity: 1;
                        transition: all 1s;
                        &.on{
                            opacity: 0;
                        }
                        img {
                            width:100%;
                            height:100%;
                        }
                    }
                    &.left-btn {
                        left:-30px;
                        opacity: 1;
                        transition: all 1s;
                        &.on{
                            opacity: 0;
                        }
                        img {
                            width:100%;
                            height:100%;
                            transform: rotate(180deg);
                        }                        
                    }
                }
            }
        }
    }
}