#main{
    width: 100%;
    position: relative;
    #section7{
        width: 450px;
        position: absolute;
        top: 30px;
        left: 450px;
        z-index: 10;
        .container{
            width: 450px;
            .modal{
                width: 100%;
                height: 100%;
                border-radius: 5px;
                overflow: hidden;
                img{
                    width: 100%;
                }
                .btn-box{
                    width: 100%;
                    height: 100%;
                    button{
                        width: 50%;
                        height: 60px;
                        border: 0;
                        cursor: pointer;
                        font-size: 16px;
                        font-weight: 500;
                        color: #000;
                        border-right: 1px solid #ccc;
                        background: #fff;
                        &:last-child{
                            border-right: 0;
                        }
                    }
                }
            }
        }
    }
}